@import url('https://fonts.googleapis.com/css2?family=Bigelow+Rules&display=swap');
body {
  width: 80%;
  max-width: 1000px;
  
  margin: 0 auto;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #36EEE0;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.title {
  font-size: 75px;
  font-weight: 900;
  color: #F652A0;
  text-align: center;
  font-family: 'Bigelow Rules', cursive;
}

.count_body {
  top: 100%;
  padding: 2em;
  background-color: #F652A0;
  text-align: center;
}

h3 {
  font-size: 5em;
  font-weight: 900;
  color: white;
}

.count_positive {
  color: green;
}
.count_negative {
  color: red;
}
.color {
  color: #fff;
}
button {
  padding: 1em;
  font-size: 1rem;
  margin: 10px;
  border: none;
  border-radius: 8px;
  color: #fff;
  font-weight: 900;
  cursor: pointer;
}

.btn_decrease {
  background-color: #F51720;
}

.btn_increase {
  background-color: green;
}
